body {
    background-color: #e8e4dd;
    -webkit-overflow-scrolling: touch;
    -webkit-transform:translate3d(0,0,0);
    -webkit-backface-visibility:hidden;
    scroll-behavior: smooth;
    -webkit-scroll-behavior: smooth;
}
.page-wrapper, #root {
  width: 100%;
}
hr {
    border-top:1px solid #0d1b21;
    width: 70%;
}
.spacer {
  height: 100vh; /* To allow scrolling */
}
.rsvp-container-zoom {
    .RSVP-link {
        font-family: 'Antic Didone';
        font-weight: 600;
        font-style: normal;
        background-color: #0d1b21 ;
        transition-duration: 0.5s;
        transition-property: background-color;
        color: #0d1b21;
        font-size: 12px;
        padding: 12px 50px;
        position: absolute;
        bottom: -65px;
        left: 50%;
        text-align: left;
        transform: translate(-50%, -50%);
        background-color: rgba(214,209,196,1);
        &:hover, &:active, &:visited, &:focus, &.active  {
            color: #0d1b21 !important;
            background-color: rgba(214,209,196,1);
            font-style: normal !important;
        }
        /**&::after {
            content: "";
            position: absolute;
            top: 5px;
            border: 1px solid #f3f1c4 !important;
            height: 42px;
            width: 128px;
            left: 8px;
            z-index: 1;
        }*/
    }
}
.zoom-image {
    width: 115%;
    height: auto;
    border-radius: 10px;
    transition: transform 0.3s ease-out;
    will-change: transform;
}
.text-container {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1001;
    width: 100%;
    height: auto;
    margin-top: 10%;
    padding: 0 10%;
    .text-title {
        font-family: 'woodley';
        font-weight: normal;
        font-size: 10em;
        color: #003026;
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        .separator {
            display: block;
            line-height: 0;
            position: relative;
            width: 70%;
            text-align: center;
            font-size: 14vh;
        }
    }
}
.RSVP-container {
    width: 100%;
    .rsvp-text-container {
        padding: 100px 0;
        text-align: center;
        h2 {
            font-family: 'Woodley';
            font-size: 9em;
            color: #f7f6e1;
            text-align: center;
            margin: 0;
        }
        p.separator {
            font-family: 'Roboto';
            font-size: 3em;
            color: #e7ae8f;
            text-align: center;
               &::before {
                content: "";
                position: absolute;
                left: 36%;
                top: 50%;
                width: 100px;
                height: 2px;
                background-color: #e7ae8f;;
            }
            &::after {
                content: "";
                position: absolute;
                right: 36%;
                top: 50%;
                width: 100px;
                height: 2px;
                background-color: #e7ae8f;;
            }
        }
        p.date-typing {
            text-align: center;
            font-size: 1.2em;
        }
        a.rsvp-link {
            margin: 50px auto 100px auto ;
            padding: 13px 25px;
            border: 1px solid #f3f1c4;
            display: inline-block;
        }
        p.desc {
            padding: 0 100px;
        }
    }
}
.navbar-light {
    background-color: #002a1e !important;
    .-mobile {
        display: none;
        img {
            height: 55px;
        }
    }
    .navbar-collapse {
        align-items: center;
    }
    .navbar-nav {
        width: 100%;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-wrap: nowrap;
        .nav-link {
            font-family: "Montserrat", serif;
            font-weight: 400;
            font-style: normal;
            font-size: 10px;
            color: #f3f1c4;
            padding-right: .5rem;
            padding-left: .5rem;
            &:hover, &:active,&:focus, &.active  {
                color: #9f9d78;
            }
        }
        .navbar-brand {
            img {
                height: 55px;
            }
        }
    }
}
#navbar {
    position: -webkit-sticky;
    position: sticky ;
    position: -moz-sticky;
    position: -ms-sticky;
    top: 0;
    overflow: hidden;
    z-index: 1000;
    &.highlight {
        position: fixed;
        top: 0;
        width: 100%;
        height: 100%;
        left: 0;
        z-index: 10001;
    }
}
.intro {
    padding: 0;
    position: relative;
    &::after {
        content: '';
        position: absolute;
        bottom: 0;
        width: 100%;
        left: 0;
        background-color: #e8e4dd;
        z-index: 1;
        height: 33vh;
    }
    &.image-container {
        height: 100vh;
        width: 100% !important;
    }
    .img-oblique-container {
        position: absolute;
        top: 50px;
        height: 80vh;
        margin: 0 auto;
        border-radius: 500px;
        overflow: hidden;
        z-index: 10;
        img {
            height: 100%;
            top: 0;
            transform: scale(1.5);
            transform-origin:197px 377px;
        }
    }
    .intro-text {
        position: absolute;
        top: 18vh;
        width: 100%;
        padding: 0 18vh;
        display: flex;
        justify-content: space-between;
        height: 57vh;
        z-index: 20;
        h2 {
            font-family: 'Antic Didone';
            font-weight: 600;
            font-style: normal;
            font-size: 35px;
            text-transform: uppercase;
            color: #cbcbcb;
            &:nth-child(3) {
                display: flex;
                justify-content: flex-end;
                align-items: flex-end;
                text-align: right;
                font-size: 24px;
                padding-bottom: 50px;
                text-transform:initial;
            }
        }
    }
}

.location-container {
    .reception-container {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: row;
        iframe {
            max-width: 100%;
            border:0;
        }
        &:nth-child(2) {
            flex-direction: row-reverse;
            .reception-text {
                padding: 50px 50px 50px 0 !important;
            }
        }
        .reception-text {
            padding: 50px;
            h1 {
            font-size: 30px;
            font-family:  'Antic Didone';
            font-weight: 600;
            font-style: normal;
            text-transform: uppercase;
            color: #f3f1c4;
            }
            h5 {
                font-family: 'Antic Didone';
                font-weight: 600;
                font-style: normal;
                text-transform: uppercase;
                color: #f3f1c4;
                margin-bottom: 25px;
            }
            p {
                font-family:  'Antic Didone';
                font-weight: 600;
                font-style: normal;
                color: #f3f1c4;
                font-size: 14px;
            }
            img.qr-code {
                width: 110px;
                height: 110px;
            }
        }
    }
}
